/* PLACEHOLDER */
.placeholder.grafico-topselection {
    min-height:300px;
    width:100%;
    background:#eee;
    display:flex;
    flex-direction:column;
    justify-content: space-around;
    align-items:center;
}
.placeholder.grafico-topselection:before {
    content:"placeholder";
    text-transform: uppercase;
    color:#fff;
}